import React from 'react';
import { Strings } from '~/config/Strings';

const TermsAndConditions = ({ darkTheme }) => {
  return (
    <div
      id="terms-policy"
      className="modal fade"
      role="dialog"
      aria-hidden="true"
    >
      <div
        className="modal-dialog modal-lg modal-dialog-centered"
        role="document"
      >
        <div
          className={
            'modal-content ' + (darkTheme ? 'bg-dark-2 text-light' : '')
          }
        >
          <div className="modal-header">
            <h5 className={'modal-title ' + (darkTheme ? 'text-white' : '')}>
              {Strings.termsConditions.title}
            </h5>
            <button
              type="button"
              className={'btn-close ' + (darkTheme ? 'btn-close-white' : '')}
              data-bs-dismiss="modal"
              aria-label="Close"
            />
          </div>
          <div className="modal-body p-4">
            <p className="textJustify">
              Bine ai venit la coala Auto CLD, operat de CLD Auto Drive
              Consult SRL. Accesul la i utilizarea site-ului nostru i a
              serviciilor noastre presupune acceptarea i respectarea acestor
              termeni i condiii. V rugm s citii cu atenie aceti termeni
              i condiii nainte de a utiliza site-ul nostru.
            </p>
            <p className="textJustify">
              coala Auto CLD, operat de CLD Auto Drive Consult SRL, nu i
              asum nicio responsabilitate pentru orice pierdere sau daun,
              inclusiv, fr limitare, daune directe, indirecte, accidentale,
              speciale, conseciniale sau exemplare, care rezult din utilizarea
              sau incapacitatea de utilizare a site-ului nostru sau a
              informaiilor coninute pe acesta.
            </p>
            <h5 className={'text-4 mt-4 ' + (darkTheme ? 'text-white' : '')}>
              Definiii
            </h5>
            <ol className="lh-lg">
              <li>
                "Noi", "nostru" sau "coala Auto CLD" se refer la CLD Auto
                Drive Consult SRL i oricare dintre filialele sale.
              </li>
              <li>"Site-ul" se refer la site-ul web al colii Auto CLD.</li>
              <li>
                "Utilizator" sau "dumneavoastr" se refer la orice persoan
                care acceseaz sau utilizeaz site-ul nostru.
              </li>
            </ol>
            <h5 className={'text-4 mt-4 ' + (darkTheme ? 'text-white' : '')}>
              Drepturile de autor
            </h5>
            <p className="textJustify">
              Coninutul site-ului nostru, inclusiv, dar fr a se limita la,
              textul, grafica, imaginile, videoclipurile, sunetele i logo-urile
              sunt proprietatea noastr sau a partenerilor notri i sunt
              protejate de legea drepturilor de autor.
            </p>
            <p className="textJustify">
              Coninutul site-ului nostru poate fi utilizat numai n scopuri
              personale i non-comerciale. Este interzis utilizarea, copierea
              sau distribuirea coninutului site-ului nostru n orice alt mod
              fr acordul nostru scris prealabil.
            </p>
            <h5 className={'text-4 mt-4 ' + (darkTheme ? 'text-white' : '')}>
              Utilizarea site-ului nostru
            </h5>
            <p className="textJustify">
              Utilizarea site-ului nostru este permis numai n scopuri legale
              i n conformitate cu aceti termeni i condiii.
            </p>
            <p className="textJustify">
              Nu suntei autorizat s utilizai site-ul nostru n niciun fel
              care ar putea s cauzeze daune sau s afecteze site-ul nostru sau
              s interfereze cu utilizarea i beneficiile oferite de site
            </p>

            <h5 className={'text-4 mt-4 ' + (darkTheme ? 'text-white' : '')}>
              Disponibilitatea site-ului
            </h5>
            <p className="textJustify">
              Ne rezervm dreptul de a restriciona accesul la site-ul nostru
              sau de a-l ntrerupe n orice moment i fr notificare
              prealabil. Nu garantm c site-ul nostru va fi disponibil n mod
              continuu sau fr erori.
            </p>
            <h5 className={'text-4 mt-4 ' + (darkTheme ? 'text-white' : '')}>
              Modificri ale termenilor i condiiilor
            </h5>
            <p className="textJustify">
              Ne rezervm dreptul de a actualiza sau modifica termenii i
              condiiile noastre n orice moment i fr notificare prealabil.
              Este responsabilitatea dumneavoastr s verificai periodic
              aceast pagin pentru a fi la curent cu modificrile. Continuarea
              utilizrii site-ului nostru dup publicarea modificrilor
              constituie acceptarea acestor modificri.
            </p>
            <h5 className={'text-4 mt-4 ' + (darkTheme ? 'text-white' : '')}>
              Legea aplicabil
            </h5>
            <p className="textJustify">
              Aceti termeni i condiii vor fi guvernai i interpretai n
              conformitate cu legile Romniei. Orice litigiu care rezult din
              sau n legtur cu aceti termeni i condiii va fi supus
              jurisdiciei exclusive a tribunalelor din Romnia.
            </p>
            <h5 className={'text-4 mt-4 ' + (darkTheme ? 'text-white' : '')}>
              Contact
            </h5>
            <p className="textJustify">
              Dac avei ntrebri sau comentarii referitoare la aceti termeni
              i condiii, v rugm s ne contactai folosind detaliile de
              contact afisate pe site.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default TermsAndConditions;
