import React from 'react';
import { Strings } from '~/config/Strings';

const GDPR = ({ darkTheme }) => {
  return (
    <div
      id="gdpr-policy"
      className="modal fade"
      role="dialog"
      aria-hidden="true"
    >
      <div
        className="modal-dialog modal-lg modal-dialog-centered"
        role="document"
      >
        <div
          className={
            'modal-content ' + (darkTheme ? 'bg-dark-2 text-light' : '')
          }
        >
          <div className="modal-header">
            <h5 className={'modal-title ' + (darkTheme ? 'text-white' : '')}>
              {Strings.gdpr.title}
            </h5>
            <button
              type="button"
              className={'btn-close ' + (darkTheme ? 'btn-close-white' : '')}
              data-bs-dismiss="modal"
              aria-label="Close"
            />
          </div>
          <div className="modal-body p-4">
            <p className="textJustify">
              Politica noastr de confidenialitate i protecie a datelor cu
              caracter personal pentru Scoala Auto CLD, operat de CLD Auto
              Drive Consult SRL, explic modul n care colectm, utilizm,
              protejm i dezvluim informaiile cu caracter personal pe care ni
              le furnizai prin intermediul site-ului nostru i a altor canale
              de comunicare.
            </p>
            <p className="textJustify">
              nelegem importana protejrii confidenialitii i a datelor cu
              caracter personal ale clienilor notri, astfel c lum msuri
              adecvate pentru a asigura protecia i securitatea informaiilor
              dvs. cu caracter personal.
            </p>
            <h5 className={'text-4 mt-4 ' + (darkTheme ? 'text-white' : '')}>
              Ce date cu caracter personal colectm?
            </h5>
            <p className="textJustify">
              n timp ce utilizai site-ul nostru sau solicitai serviciile
              noastre, putem colecta urmtoarele tipuri de date cu caracter
              personal:
              <ul className="lh-lg">
                <li>Nume i prenume</li>
                <li>Adresa de e-mail</li>
                <li>Numrul de telefon</li>
              </ul>
            </p>
            <h5 className={'text-4 mt-4 ' + (darkTheme ? 'text-white' : '')}>
              De ce colectm datele dvs. cu caracter personal?
            </h5>
            <p className="textJustify">
              Colectm i utilizm datele dvs. cu caracter personal pentru:
              <ul className="lh-lg">
                <li>A v asista n utilizarea serviciilor noastre</li>
                <li>
                  A uura procesul de nregistra la coala noastr de oferi
                </li>
                <li>
                  A facilita procesul de programare pentru orele de instrure
                  teoretic sau practic
                </li>
              </ul>
            </p>
            <h5 className={'text-4 mt-4 ' + (darkTheme ? 'text-white' : '')}>
              Cum protejm datele dvs. cu caracter personal?
            </h5>
            <p className="textJustify">
              Pentru a proteja informaiile dvs. cu caracter personal, utilizm
              msuri de securitate adecvate, inclusiv controlul accesului i
              securitatea reelei. De asemenea, angajaii notri sunt instruii
              s protejeze confidenialitatea i s pstreze secretele
              comerciale.
            </p>
            <h5 className={'text-4 mt-4 ' + (darkTheme ? 'text-white' : '')}>
              Ct timp pstrm datele dvs. cu caracter personal?
            </h5>
            <p className="textJustify">
              Vom pstra datele dvs. cu caracter personal numai pentru perioada
              necesar pentru a ndeplini scopurile pentru care le-am colectat
              sau pentru a ndeplini obligaiile legale.
            </p>
            <h5 className={'text-4 mt-4 ' + (darkTheme ? 'text-white' : '')}>
              Drepturile dvs. n ceea ce privete datele cu caracter personal
            </h5>
            <p className="textJustify">
              Avei dreptul s ne solicitai accesul, rectificarea sau tergerea
              datelor cu caracter personal pe care le deinem despre dvs. De
              asemenea, avei dreptul s ne solicitai s ncetm s v trimitem
              comunicri de marketing. Pentru a solicita aceste drepturi, v
              rugm s ne contactai utiliznd detaliile de contact furnizate pe
              site-ul nostru.
            </p>
            <h5 className={'text-4 mt-4 ' + (darkTheme ? 'text-white' : '')}>
              Modificri ale Politicii de Confidenialitate
            </h5>
            <p className="textJustify">
              Politica noastr de confidenialitate poate fi actualizat
              periodic. Orice modificare a acestei politici va fi publicat pe
              site-ul nostru i va intra n vigoare imediat dup publicarea sa.
              V recomandm s verificai periodic aceast pagin pentru a fi la
              curent cu orice modificare a politicii noastre de
              confidenialitate.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default GDPR;
