import React from 'react';
import { Strings } from '~/config/Strings';
import { useFeature } from 'flagged';

const Services = () => {
  const darkTheme = useFeature('darkTheme');
  const classicHeader = useFeature('classicHeader');

  // services details
  const services = [
    {
      name: 'Instruire Practic',
      desc: 'Oferim ore de condus de cea mai bun calitate alturi de instructori experimentai pregtii s te ghideze prin toate aspectele conducerii automobilelor.',
      icon: 'fa-solid fa-road-circle-check fa-lg',
    },
    {
      name: 'Pregtire Teoretic',
      desc: 'Efectuarea unui instructaj teoretic complet in vederea promovarii examenului teoretic, impreuna cu lectii de prim ajutor.',
      icon: 'fa-solid fa-file-circle-check fa-lg',
    },
    {
      name: 'Program Flexibil',
      desc: 'Indiferent de ct de aglomerat este programul tu, suntem aici s te ajutm s i atingi obiectivele de conducere. Putem s adaptm orele de studiu n funcie de nevoile tale.',
      icon: 'fa-solid fa-calendar-check fa-lg',
    },
    {
      name: 'ntocmirea Dosarului',
      desc: 'Noi suntem pregtii pentru a te ghida prin ntregul proces, oferidu-i informaiile i sfaturile necesare pentru a v pregti dosarul ntr-un mod eficient i corect.',
      icon: 'fa-solid fa-file-signature fa-lg',
    },
    {
      name: 'Flot modern',
      desc: 'Oferim o flot de autovehicule moderne i fiabile. Mainile noastre sunt echipate cu tehnologii de actualitate, astfel nct s poi avea o experien sigur i confortabil.',
      icon: 'fa-solid fa-car-side fa-lg',
    },
    {
      name: 'Pre avantajos',
      desc: 'Oferim un pre avantajos i competitiv pentru orele de conducere i studiu. Suntem dedicai s v oferim o experien de nvare accesibil i de calitate.',
      icon: 'fa-solid fa-piggy-bank fa-lg',
    },
  ];

  return (
    <section
      id="services"
      className={'section ' + (darkTheme ? 'bg-dark-2' : 'bg-light')}
    >
      <div className={'container ' + (classicHeader ? '' : 'px-lg-5')}>
        {/* Heading */}
        <div className="position-relative d-flex text-center mb-5">
          <h2
            className={
              'text-24  text-uppercase fw-600 w-100 mb-0 ' +
              (darkTheme ? 'text-white-50  opacity-1' : 'text-light  opacity-4')
            }
          >
            {Strings.services.backgroundTitle}
          </h2>
          <p
            className={
              'text-9  fw-600 position-absolute w-100 align-self-center lh-base mb-0 ' +
              (darkTheme ? 'text-white' : 'text-dark')
            }
          >
            {Strings.services.mainTitle}
            <span className="heading-separator-line border-bottom border-3 border-primary d-block mx-auto" />
          </p>
        </div>
        {/* Heading end*/}
        {/* content start */}
        <div className="row">
          <div className="col-lg-11 mx-auto">
            <div className="row">
              {services.length > 0 &&
                services.map((service, index) => (
                  <div className="col-md-6" key={index}>
                    <div className="featured-box style-3 mb-5 mx-1 textJustify ">
                      <div
                        className={
                          'featured-box-icon text-primary shadow-sm rounded ' +
                          (darkTheme ? 'bg-dark-1' : 'bg-white')
                        }
                        style={{
                          top: 0,
                          bottom: 0,
                          margin: 'auto',
                        }}
                      >
                        <i className={service.icon} />
                      </div>
                      <h3 className={darkTheme ? 'text-white' : ''}>
                        {service.name}
                      </h3>
                      <p
                        className={'mb-0 ' + (darkTheme ? 'text-white-50' : '')}
                      >
                        {service.desc}
                      </p>
                    </div>
                  </div>
                ))}
            </div>
          </div>
        </div>
        {/* content end */}
      </div>
    </section>
  );
};

export default Services;
